import React, { Component } from "react";
import Blackbox from "../Blackbox/Blackbox";
import ScrollHelper from "../ScrollHelper/ScrollHelper";
import "./Gallery.scss";
import ExitDummy from "../ExitDummy/ExitDummy";
import Lightbox from "react-image-lightbox";
import "react-image-lightbox/style.css";

export default class Gallery extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currImg: 0,
      viewerIsOpen: false,
      galleryIsOpen: false,
      initialImageNumber: 8
    };
  }

  gotoNext = () => {
    this.setState({
      currImg: this.state.currImg + 1
    });
  };

  gotoPrevious = () => {
    this.setState({
      currImg: this.state.currImg - 1
    });
  };

  closeViewer = () => {
    this.setState({
      viewerIsOpen: false
    });
  };

  openViewer = (event, index) => {
    this.setState(
      {
        currImg: index
      },
      this.setState({
        viewerIsOpen: true
      })
    );
  };

  openGallery = () => {
    this.setState({
      galleryIsOpen: true
    });
    this.props.openGallery();
  };

  closeGallery = event => {
    if (!event.target.classList.contains("gallery-thumbnail")) {
      this.setState({
        galleryIsOpen: false
      });
      this.props.closeGallery();
    }
  };
  render() {
    const thumbnails = [...Array(41).keys()].map((image, index) => (
      <img
        key={index}
        src={require(`./Images/${index + 1}.jpg`)}
        className="gallery-thumbnail"
        alt=""
        onClick={e => {
          this.openViewer(e, index);
        }}
        style={{ cursor: "pointer" }}
      />
    ));
    const imageSet = [...Array(41).keys()].map((image, index) => {
      return require(`./Images/${index + 1}.jpg`);
    });
    const { currImg, viewerIsOpen } = this.state;
    return (
      <>
        <section className="page-gallery">
          <div className="container">
            <button onClick={this.openGallery}>
              <div className="gallery-button-darken">
                <div className="gallery-mock-button">
                  <span>GALERIA</span>
                </div>
              </div>
            </button>
          </div>
          <Blackbox
            isOn={this.state.galleryIsOpen ? true : false}
            clickAction={this.closeGallery}
            height={"-100vh"}
          >
            <ExitDummy />
            <div
              className="page-gallery-thumbnails"
              style={viewerIsOpen ? { overflow: "hidden" } : { overflow: "" }}
            >
              {thumbnails}
            </div>
          </Blackbox>
          <ScrollHelper className="page-footer-scroll" height="100px" />
          {viewerIsOpen && (
            <Lightbox
              mainSrc={imageSet[currImg]}
              nextSrc={imageSet[(currImg + 1) % imageSet.length]}
              prevSrc={
                imageSet[(currImg + imageSet.length - 1) % imageSet.length]
              }
              onCloseRequest={() => this.setState({ viewerIsOpen: false })}
              onMovePrevRequest={() =>
                this.setState({
                  currImg: (currImg + imageSet.length - 1) % imageSet.length
                })
              }
              onMoveNextRequest={() =>
                this.setState({
                  currImg: (currImg + 1) % imageSet.length
                })
              }
              reactModalStyle={{
                overlay: {
                  zIndex: "10000"
                }
              }}
              enableZoom={false}
            />
          )}
        </section>
      </>
    );
  }
}
