import React from "react";
import "./Header.scss";
import Logo from "./Logo/Logo";
import Menu from "./Menu/Menu";
import ScrollHelper from "../ScrollHelper/ScrollHelper";

export default ({
  openExtendedInfo,
  closeExtendedInfo,
  openShowInfo,
  closeShowInfo,
  openOfferInfo,
  closeOfferInfo
}) => {
  return (
    <>
      <ScrollHelper height="100px" />
      <header className="page-header sticky">
        <div className="container page-header-internal">
          <Logo />
          <Menu
            openExtendedInfo={openExtendedInfo}
            closeExtendedInfo={closeExtendedInfo}
            openShowInfo={openShowInfo}
            closeShowInfo={closeShowInfo}
            openOfferInfo={openOfferInfo}
            closeOfferInfo={closeOfferInfo}
          />
        </div>
      </header>
    </>
  );
};
