import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import "./Footer.scss";

export default ({ icons }) => {
  return (
    <footer className="page-footer">
      <div className="container footer-container">
        <div className="footer-email">
          <a href="mailto:treblers@treblers.pl">
            <FontAwesomeIcon icon={icons.mail} size="2x" />
            <span>&nbsp;: treblers@treblers.pl</span>
          </a>
        </div>
        <div className="footer-phone">
          <a href="tel:0048-791-331-232">
            <FontAwesomeIcon icon={icons.phone} size="2x" />
            <span>&nbsp;: +48 791 331 232 (Maria Jagaciak)</span>
          </a>
        </div>
        <div className="footer-icons">
          <a
            href="https://www.youtube.com/channel/UCsTwnnYrrRD43gKz2XFLzYQ"
            target="_blank"
            rel="noopener noreferrer"
          >
            <FontAwesomeIcon icon={icons.youtube} size="2x" />
          </a>
          <a
            href="https://pl-pl.facebook.com/treblersi/"
            target="_blank"
            rel="noopener noreferrer"
          >
            <FontAwesomeIcon icon={icons.facebook} size="2x" />
          </a>
          <a
            href="https://www.instagram.com/treblerspl/"
            target="_blank"
            rel="noopener noreferrer"
          >
            <FontAwesomeIcon icon={icons.instagram} size="2x" />
          </a>
        </div>
      </div>
      <div className="container">
        <span
          className="copyright-info"
          style={{
            fontSize: "10px",
            display: "block",
            textAlign: "right",
            marginTop: "5px",
            marginBottom: "0"
          }}
        >
          &copy; 2019 Maciej Kozłowski
        </span>
      </div>
    </footer>
  );
};
