import React, { Component } from "react";
import Blackbox from "../../Blackbox/Blackbox";
import ExitDummy from "../../ExitDummy/ExitDummy";
import showList from "./showList.json";
import "./Shows.scss";

export default class componentName extends Component {
  closeShows = () => {
    this.props.clickAction();
  };

  displayShowItem = (item, index) => {
    let link = null;
    if (item.linkUrl) {
      link = (
        <a href={item.linkUrl} rel="noopener noreferrer" target="_blank">
          {item.linkDesc}
        </a>
      );
    }
    return (
      <div key={index} className="show-item">
        <h3>
          {item.date}: {item.title}
        </h3>
        <p className="show-item-description">
          {item.description}
          &nbsp;
          {link}
        </p>
        <hr />
      </div>
    );
  };
  render() {
    const pastShows = showList
      .filter(item => !item.future)
      .sort((a, b) => new Date(b.date) - new Date(a.date))
      .map(this.displayShowItem);
    const futureShows = showList
      .filter(item => item.future)
      .sort((a, b) => new Date(a.date) - new Date(b.date))
      .map(this.displayShowItem);
    console.log();
    return (
      <div
        style={{
          position: "fixed",
          top: "0",
          left: "0"
        }}
      >
        <Blackbox
          isOn={this.props.isShowsOn}
          clickAction={this.props.clickAction}
          height="-100vh"
        >
          <div className="container">
            <div
              className="page-info-extended"
              onClick={e => e.stopPropagation()}
            >
              <ExitDummy exit={this.props.clickAction} />
              <h2>Nadchodzące koncerty</h2>
              {futureShows.length ? (
                futureShows
              ) : (
                <p
                  style={{
                    fontWeight: 300
                  }}
                >
                  <em>Więcej informacji wkrótce!</em>
                </p>
              )}
              <h2
                style={{
                  marginTop: "50px"
                }}
              >
                Kalendarium
              </h2>
              {pastShows}
            </div>
          </div>
        </Blackbox>
      </div>
    );
  }
}
