import React from "react";
import "./Menu.scss";
import MenuItem from "./MenuItem/MenuItem";
import Shows from "../../Info/Shows/Shows";

export default class Menu extends React.Component {
  state = {
    isMenuVisible: false,
    areShowsOn: false
  };

  toggleMenu = () => {
    this.setState({
      isMenuVisible: !this.state.isMenuVisible
    });
  };
  openMenu = () => {
    if (!this.state.isMenuVisible) {
      this.setState({ isMenuVisible: true });
    } else {
      this.setState({ isMenuVisible: false });
    }
  };
  openShows = () => {
    this.setState(
      {
        areShowsOn: true
      },
      () => document.body.classList.add("locked")
    );
  };
  closeShows = () => {
    this.setState(
      {
        areShowsOn: false
      },
      () => document.body.classList.remove("locked")
    );
  };

  componentDidMount() {
    document.addEventListener("touchend", e => {
      if (
        !e.target.classList.contains("hamburger-line") &&
        !e.target.classList.contains("hamburger-icon")
      ) {
        this.setState({ isMenuVisible: false });
      }
    });
  }

  render() {
    const { openShowInfo, openOfferInfo } = this.props;

    return (
      <nav className={`menu ${this.state.isMenuVisible ? "menu-show" : ""}`}>
        <div className="hamburger-icon" onClick={this.openMenu}>
          <div className="hamburger-line line-top" />
          <div className="hamburger-line line-middle" />
          <div className="hamburger-line line-bottom" />
        </div>
        <ul>
          <div className="small-screen-gap" />
          <MenuItem
            contents="O NAS"
            selector=".page-info-card-scroll"
            clickSelector=".page-info"
            toggleHamburger={this.toggleMenu}
          />
          <MenuItem
            contents="SHOW"
            selector=".page-info-card-scroll"
            clickSelector=".feel-the-reel"
            toggleHamburger={this.toggleMenu}
            clickAction={openShowInfo}
          />
          <MenuItem
            contents="OFERTA"
            selector=".page-info-card-scroll"
            clickSelector=".offer"
            toggleHamburger={this.toggleMenu}
            clickAction={openOfferInfo}
          />
          <MenuItem
            contents="FILMY"
            selector=".page-videos-scroll"
            toggleHamburger={this.toggleMenu}
          />
          <MenuItem
            contents="GALERIA"
            selector=".page-gallery-scroll"
            toggleHamburger={this.toggleMenu}
          />
          <MenuItem
            contents="KONCERTY"
            toggleHamburger={this.toggleMenu}
            clickAction={this.openShows}
          />
          <MenuItem
            contents="KONTAKT"
            selector=".page-footer-scroll"
            toggleHamburger={this.toggleMenu}
          />
        </ul>
        <Shows
          clickAction={this.closeShows}
          isShowsOn={this.state.areShowsOn}
        />
      </nav>
    );
  }
}
