import React from "react";
import "./Blackbox.scss";

export default ({ isOn, height, children, clickAction }) => {
  return (
    <div
      className="helper-blackbox"
      style={{
        top: isOn ? 0 : height,
        opacity: isOn ? 1 : 0
      }}
      onClick={clickAction}
    >
      {children}
    </div>
  );
};
