import React from "react";
import "./Slide.scss";

export default ({ isVisible, imageToDisplay, quote, quoteAuthor }) => {
  return (
    <div
      className="page-slider-slide"
      style={{
        zIndex: isVisible ? 0 : -1000,
        opacity: isVisible ? 1 : 0,
        transition: "0.8s"
      }}
    >
      <div
        style={{
          background: `url(${imageToDisplay}) no-repeat`,
          backgroundSize: "cover",
          backgroundPosition: "center 40%",
          width: "100%",
          height: "100%",
          position: "relative"
        }}
      >
        <div className="slide-quote">
          <p>{quote}</p>
          <h3>{quoteAuthor}</h3>
        </div>
      </div>
    </div>
  );
};
